import React, { useContext } from "react"
import { AppContext } from "./Context"

export function ObjectList() {
    const { state, dispatch } = useContext(AppContext)!

    return (
        <div>
            <h1>Objects Count: {state.objects.length}</h1>
            <input type="button" value="Add" onClick={() => dispatch({ type: "addObject" })} />
            <input type="button" value="Remove" onClick={() => dispatch({ type: "removeObject" })} />
            <div>
                {state.objects.map((object, objectIndex) => {
                    return (
                        <div key={objectIndex} style={{ border: "2px solid black", padding: "4px", margin: "4px" }}>
                            <div>Object {objectIndex}</div>
                            {state.objectProperties.map(objectProperty => {
                                return (
                                    <div key={objectProperty.name}>
                                        <label>{objectProperty.name}: </label>
                                        <input type="text" value={object.propertyValues[objectProperty.name]} onChange={e => {
                                            if (e.target.value === "") {
                                                const propertyValues = { ...object.propertyValues }
                                                delete propertyValues[objectProperty.name]

                                                dispatch({
                                                    type: "updateObject",
                                                    index: objectIndex,
                                                    object: {
                                                        ...object,
                                                        propertyValues: propertyValues
                                                    }
                                                })
                                            } else {
                                                const value = parseFloat(e.target.value)
                                                if (!isNaN(value)) {
                                                    dispatch({
                                                        type: "updateObject",
                                                        index: objectIndex,
                                                        object: {
                                                            ...object,
                                                            propertyValues: {
                                                                ...object.propertyValues,
                                                                [objectProperty.name]: value
                                                            }
                                                        }
                                                    })
                                                }
                                            }
                                        }} />
                                    </div>
                                )
                            })}
                        </div>
                    )
                })}
            </div>
        </div>
    )
}